/* index.css */
.flex {
    display: flex;
  }
  .relative {
    position: relative;
  }
  .absolute {
    position: absolute;
  }
  .transition-all {
    transition: all 0.5s ease-in-out;
  }
  .space-y-8 > :not(:last-child) {
    margin-bottom: 2rem;
  }
  .transform {
    transform: translateX(0);
  }
  .transform.-translate-x-full {
    transform: translateX(-120%);
  }
  .transform.translate-x-full {
    transform: translateX(120%);
  }
  